<template>
    <div :class = "showDetail? 'icon-bar' : 'icon-bar-small'">
    <div :class="showDetail? 'arrow' : 'arrow-inverse'" @click="showDetail = !showDetail"><i class="fa-solid fa-angles-left"></i></div>
      <div :class = "showDetail? 'icon-list' : 'icon-list-summary'" >
        <div class="icon" @click="click('profile')"><i class="fa-solid fa-user " /><div :class="showDetail? 'menu' : 'hidden-menu'">profile</div></div>
        <div class="icon" @click="click('education')"><i class="fa-solid fa-school" /><div :class="showDetail? 'menu' : 'hidden-menu'">education</div></div>
        <div class="icon" @click="click('experience')"><i class="fa-solid fa-briefcase" /><div :class="showDetail? 'menu' : 'hidden-menu'">experience</div></div>
        <div class="icon" @click="click('skill')"><i class="fa-solid fa-code" /><div :class="showDetail? 'menu' : 'hidden-menu'">skill</div></div>
        <div class="icon" @click="click('interest')"><i class="fa-solid fa-star" /><div :class="showDetail? 'menu' : 'hidden-menu'">interest</div></div>
      </div>
    </div>
</template>

<script setup>

import {ref, watch, defineEmits, onMounted} from "vue";

onMounted(() => {
  if(window.innerWidth < 1000) showDetail.value = false;
})

const showDetail = ref(true);
const emit = defineEmits(['resize', 'click']);

watch(showDetail, (n) => {
  emit("resize", !n);
})

window.addEventListener('resize', () => {
  if(window.innerWidth < 1000) showDetail.value = false;
})


const click = (id) => {
  emit("click", id);
}
</script>

<style lang="scss">
@import "../style/SideBar.scss";
</style>